require('async')
const axios = require('axios')
const isUrl = require('is-url')

/**
 * @classdesc Represents an API call.
 * @class
 * @abstract
 */
class APICall {
    /**
     * Create a APICall.
     * @constructor
     * @param {string} baseURL - A string with the base URL for account.
     * @param {string} appKey - A string with the public app key.
     * @param {string} accessToken - A string with the JWT accessToken.
     * @param {string} query - A GraphQL query string.
     */
    constructor(baseURL, appKey, accessToken) {
        if (!isUrl(baseURL)) throw new Error('The base URL provided is not valid');

        this.baseURL = baseURL;
        this.appKey = appKey;
        this.accessToken = accessToken;
    }

    /**
     * Fetch the information from the API.
     * @return {Promise} - Returns a Promise that, when fulfilled, will either return an JSON Object with the requested
     * data or an Error with the problem.
     */
    send(query, variables) {

        axios.defaults.crossDomain = true
		axios.defaults.headers.common['App-Key'] = this.appKey
		axios.defaults.headers.common['Authorization'] = 'Bearer ' + this.accessToken

        return axios.post(this.baseURL, {
			query: query,
            variables: variables
		})
		.then((response) => {
            if (response.status >= 400) { // check for 4XX, 5XX, wtv
                return Promise.reject({
                    status: response.status,
                    message: response.statusText
                });
            }
            if (response.status >= 200 && response.status <= 202) {
                return response.data;
            }
            return {};
        });
    }
}

/**
 * @classdesc Represents the blacksheep SDK. It allows the user to make every call to the API with a single function.
 * @class
 */
export default class bs {
    /**
     * Create blacksheep SDK.
     * @constructor
     * @param {String} options.accessToken - The JWT token.
     * @param {String} options.baseURL - The URL with the account domain.
     * @param {String} options.appKey - The public app key.
     * @param {Object} options - An object containing the app key, access token and the base URL.
     */
    constructor(options) {

        this.api = new APICall(
            options.baseURL,
            options.appKey,
            options.accessToken
        );
    }

    /**
     * User login.
     * @param {Object} params={} - An object containing the credentials with which the user intends to login.
     * @param {String} params.email - The email of the user.
     * @param {String} params.password - The password of the user.
     * @return {Promise} User - Returns a Promise that, when fulfilled, will either return an Object with the
     * user and JWT token or an Error with the problem.
     */
    login(params) {

        return this.api.send(`
			mutation {
				login (email: "${params.email}", password: "${params.password}")
				{   
                    id,
					jwt
				}
			}
		`)
		.then(response => {
			if (response.errors) {
				return Promise.reject(response.errors);
			}
			return response.data.login
		})
    }

    /**
     * Get Shops
     * @return {Promise} Shops - Returns a Promise that, when fulfilled, will either return an Array of Objects with the
     * shop id, name and brand or an Error with the problem.
     */
    getShops() {

        return this.api.send(`
            { 
                shops { 
                    list { 
                        id, 
                        name, 
                        brand {
                            name
                        }
                    }
                }
            }
        `)
        .then(response => {
            if (response.errors) {
                // console.log(response.errors)
                return Promise.reject(response.errors);
            }
            // console.log(response.data)
            return response.data.shops.list
        })
    }

    /**
     * Get User
     * @param {String} id - The id of the user.
     * @return {Promise} User - Returns a Promise that, when fulfilled, will either return an Object with the
     * user info or an Error with the problem.
     */
    getUser(id) {

        return this.api.send(`
            { 
                user (id: "${id}") { 
                    type
                    firstname
                    email
                }
            }
        `)
        .then(response => {
            if (response.errors) {
                // console.log(response.errors)
                return Promise.reject(response.errors);
            }
            // console.log(response.data)
            return response.data.user
        })
    }

    /**
     * Get Categories
     * @param {String} shopId - The id of the current shop.
     * @return {Promise} Categories - Returns a Promise that, when fulfilled, will either return an Array with the
     * shop categories or an Error with the problem.
     */
    getCategories(shopId) {

        return this.api.send(`
            {
              categories
              (
                shopId: "${shopId}"
              )
              {
                id,
                name,
                type,
                displayName
              }
            }
        `)
        .then(response => {
            if (response.errors) {
                // console.log(response.errors)
                return Promise.reject(response.errors);
            }
            // console.log(response.data)
            return response.data.categories
        })
    }

    /**
     * Get categoryItems
     * @param {String} categoryId - The id of the category.
     * @return {Promise} Categories - Returns a Promise that, when fulfilled, will either return an Array with the
     * category's items or an Error with the problem.
     */
    getCategoryItems(categoryId) {

        return this.api.send(`
            {
              items
              (
                categoryId: "${categoryId}"
              )
              {
                id,
                name,
                price,
                displayName,
                pictureUrl,
                modifiers {
                    id,
                    name
                }
              }
            }
        `)
        .then(response => {
            if (response.errors) {
                // console.log(response.errors)
                return Promise.reject(response.errors);
            }
            // console.log(response.data)
            return response.data.items
        })
    }

    /**
     * Update Categories
     * @param {Object} category={} - An object containing the category to be updated
     * @return {Promise} Category - Returns a Promise that, when fulfilled, will either return an Object with the
     * updated category or an Error with the problem.
     */
    updateCategory(category) {
        return this.api.send(`
            mutation ($category: CategoryInput!) {
                updateCategory
                (
                    category: $category
                )
                {
                    id,
                    name,
                    type,
                    displayName
                }
            }
        `, {category: category})
        .then(response => {
            if (response.errors) {
                // console.log(response.errors)
                return Promise.reject(response.errors);
            }
            // console.log(response.data)
            return response.data.updateCategory
        })
    }

    /**
     * Update Item
     * @param {Object} item={} - An object containing the item to be updated
     * @return {Promise} Category - Returns a Promise that, when fulfilled, will either return an Object with the
     * updated item or an Error with the problem.
     */
    updateItem(item) {
        return this.api.send(`
            mutation ($item: ItemInput!) {
                updateItem
                (
                    item: $item
                )
                {
                    id,
                    name,
                    displayName
                }
            }
        `, {item: item})
        .then(response => {
            if (response.errors) {
                // console.log(response.errors)
                return Promise.reject(response.errors);
            }
            // console.log(response.data)
            return response.data.updateItem
        })
    }

    /**
     * Get Item
     * @param {String} id - The id of the item to get
     * @return {Promise} Category - Returns a Promise that, when fulfilled, will either return an Object with the
     * item or an Error with the problem.
     */
    getItem(id) {
        return this.api.send(`
            {
                item
                (
                    id: "${id}"
                )
                {
                    id,
                    name,
                    price,
                    displayName,
                    pictureUrl,
                    modifiers {
                        id,
                        name
                    },
                    category {
                        id
                    }
                }
            }
        `)
        .then(response => {
            if (response.errors) {
                // console.log(response.errors)
                return Promise.reject(response.errors);
            }
            // console.log(response.data)
            return response.data.item
        })
    }

}


